	module.exports = {"data":{"markdownRemark":{"html":"<p>\"ディジング\"を見直した後、 \"放棄された首都\"の後、 \"放棄された首都\"の後に本と呼ばれるこの本は、 \"ディジング\"を主張し、主人公はまた、 \"放棄された首都\"です。このスタイルと技術は、「放棄された資本」を模倣しています。多くの「ここの著者は5つの十字を削除する」などもあります。しかし、この本は美術と領域ではエレガントではありません。そのような本はまだ人々の好奇心を利用しようとするある誘惑を持っている。現在、中国は書籍市場を形成していますが、それは良書市場ではありません。正式な書籍チャンネル以外にも、大量の書籍が配布される大規模で機密性の高い図書館があります。全国の都市では、多数の本屋があり、さまざまな奇妙な本を提供しています。これは主に一般読者を引き付けるために使用されています。政治的に敏感であるか、または芸術で論争の的になっている本もあります。書籍を販売するほとんどの人は、書籍の価値を知ることができないかもしれません。彼らが本について書いたことは、基本的なテーマについてはっきりしていません。彼らが判断する唯一の基準は、市場が売却され、売却されるということです。</p>\n<p>これは、本が市場を持つことができることを示しています、問題は、良い本を市場に公開する方法です。市場を開拓するための基本的な方法の1つは、良い本を心理的な好みにして、ある種の知識を形成し、ある種の知識の魅力を形成することです。人々は知識と思考を必要としませんが、人々は常に知識と選択肢を選択します。また、知識と思考の問題では、人々は社会的指導なしに群衆に従う必要があり、人々は新しい知識や新しいアイデアを決して選ぶことはありません。実用主義が知識と思考の観点から人々の選択を促進することは不可能であり、そうであれば、おそらくは真実であり、知識と思考は実用主義を選ぶでしょう。</p>\n<p>\"...\"テクニックに関しては、本当に本を書いて100語を書いてから40万語を削除したいと思っています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0111.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0111.md","frontmatter":{"title":"6月11日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0111.md absPath of file >>> MarkdownRemark"}}

